// scss-docs-start font-variables
// stylelint-disable value-keyword-case
$font-family-sans-serif: "Inter" !default;
$white: #FFFFFF;
$yellow: #FDA61D;
$blue: #1599BE;
$red: #FF6868;
$light-grey: #DEDEDE;
$grey: #6D6D6D;
$black-70: #464646;
$black: #1F211E;
// font sizes
$font-size-base: .75rem;
$line-height-base: 1;
$line-height-sm: 1.5;
$h1-font-size: 3.125rem;
$headings-line-height: 1;
$headings-font-weight: 800;
$headings-color: $light-grey;
$font-weight-medium: 500;
$font-sizes: ( 1: 1.5rem, 2: 1rem);
$spacer: 1rem;
$spacers: ( 0: 0, 1: $spacer * .25, 2: $spacer * .5, 3: $spacer, 4: $spacer * 1.5, 5: $spacer * 3, 6: $spacer * 7.25, 7: $spacer * 18.375);
// break points
$grid-breakpoints: (xxc:0, xs: 390px, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1440px, xxxl: 1548px, xxxxl: 1920px);
// containers
$container-max-widths: (xc: 100%, sm: 540px, md: 720px, lg: 760px, xl: 1008px, xxl: 1116px, xxxl: 1375px, xxxxl: 1547px);
// border
$border-radius-sm: .625rem!default;
// buttons
$btn-font-weight: 600;
$btn-font-size: 1.25rem;
$btn-padding-x: 2rem;
$btn-padding-y: 1.125rem;
$btn-color: $white!default;
// form-control
$input-font-size: 1rem !default;
$input-line-height: 1 !default;
$input-font-weight: 500 !default;
$input-color: $grey !default;
$input-bg: $white !default;
$input-border-color: $white !default;
$input-padding-y: 1.025rem !default;
$input-padding-x: 1rem !default;
$input-group-addon-color: $white !default;
$input-group-addon-border-color: transparent !default;
$input-group-addon-bg: transparent !default;
$form-feedback-invalid-color: $red !default;
$form-feedback-font-size: .625rem !default;
$input-box-shadow: none !default;
$input-focus-box-shadow: none !default;
